.login-container {
background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 1rem;
}

.login-box {
  background: white;
  border-radius: 20px;
  padding: 2rem;
  width: 400px;
  max-width: 95%;
  box-shadow: 0px 10px 25px rgba(0,0,0,0.1);
  text-align: center;
}

.login-box h2 {
  margin-bottom: 0.5rem;
  color: #333;
}

.login-subtext {
  margin-bottom: 1.5rem;
  color: #555;
  font-size: 0.9rem;
}

.input-group {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  border-radius: 30px;
  padding: 0.5rem 1rem;
  background: #f9f9f9;
}

.input-group input {
  border: none;
  outline: none;
  flex: 1;
  padding: 0.5rem;
  background: transparent;
  font-size: 1rem;
}

.input-icon {
  margin-right: 0.5rem;
  color: #04053f;
}

.forgot-link {
  text-align: right;
  margin-bottom: 1rem;
  font-size: 0.85rem;
}

.login-btn {
  background: linear-gradient(135deg, #051aa3, #010307);
  border-radius: 30px;
  padding: 0.8rem;
  font-weight: bold;
  color: white;
  border: none;
  width: 100%;
  cursor: pointer;
  margin-bottom: 1rem;
  transition: all 0.3s ease;
}

.login-btn:hover {
  background-color: #5848c2;
}

.signup-text {
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
}

.signup-text span {
  cursor: pointer;
}

.social-login {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 0.5rem;
  font-size: 1.2rem;
  cursor: pointer;
  color: #102d6dff;
}

.login-message {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #f5f5f5;
  border-radius: 15px;
  padding: 1rem 2rem;
  box-shadow: 0px 5px 15px rgba(0,0,0,0.2);
  animation: fadeInOut 3s forwards;
}

@keyframes fadeInOut {
  0%, 20% {opacity: 0;}
  30%, 70% {opacity: 1;}
  80%, 100% {opacity: 0;}
}
